<template>
    <div>
        <p>{{ msg }}</p>
        <p @click="callApi()">{{ msg }}</p>
    </div>
</template>

<script>
    export default {
        name: 'Home',
        data() {
            return {
                msg: 'Home | Home'
            }
        },
        methods: {
            callApi() {
                this.$http.get('/api/a').then(response => {
                    this.msg = response;
                })
            }
        }
    }
</script>



